.text-primary {
  color: var(--main-accent-color);
}

h1, h2, h3, h4, h5, h6, h7 {
  font-weight: 700;
}

h1 {
  font-size: 5rem;
  line-height: 120%;
  letter-spacing: 0.03em;
}

h2 {
  font-size: 2.5rem;
  line-height: 2.7rem;
  letter-spacing: 0.03em;

  @media (min-width: 992px) {
    font-size: 3rem;
    line-height: 3.5rem;
  }
}
